@import url('https://fonts.googleapis.com/css2?family=Overpass+Mono:wght@300..700&display=swap');

body {
  margin: 0;
  font-family: "Overpass Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #EEEDEA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  display: flex;
  flex-direction: column;
}

.nav {
  display: flex;
  border-block: solid black 3px;
  align-items: center;
  padding-inline: 10px;
}


.heading {
  display: flex;
  padding-inline: 116px;
  padding-block: 40px;
}

.heading>.left {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.experience {
  border-block: solid black 3px;
  padding-inline: 116px;
  padding-block: 40px;
}

.experience>.collection {}

.experience>.collection>.item {
  padding-inline: 34px;
  padding-block: 20px;
  gap: 28px;
  display: flex;
  background-color: #C5A1FF;
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  align-items: center;
}

.skills {
  padding-inline: 116px;
  padding-block: 40px;
  display: flex;
  flex-direction: column;
  gap: 23px;
}

.skills>.subcontainer {
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.skill_container {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.skillitem {
  background-color: white;
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  display: flex;
  align-items: center;
  padding-inline: 27px;
  padding-block: 14px;
  gap: 8px;
  transition-duration: 0.2s;
}

.skillitem:hover {
  box-shadow: 0px 0px 0px 0px #000000;
  transform: translate(4px, 4px);
  cursor: pointer;
}

.skills>.subcontainer>h2 {
  margin: 0;
}

.skillitem>h3,
.skillitem>h4,
.skills>.subcontainer>h3 {
  margin: 0;
  width: max-content;
}

h1,
h2,
h3,
h4,
p {
  text-align: start;
}

p {
  font-weight: 600;
}

.projects {
  display: flex;
  padding-inline: 116px;
  padding-block: 40px;
  border-block: solid black 3px;
  flex-direction: column;
}

.projects>.projectsContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 23px;
}

.projects>.projectsContainer>.project_item {
  background-color: #CFEE30;
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  padding: 30px;
  max-width: 25%;
}

.projects>.projectsContainer>.project_item>.top {
  display: flex;
  gap: 20px;
  align-items: center;
}

.projects>.projectsContainer>.project_item>.middle {}

.projects>.projectsContainer>.project_item>.middle>p {}

.projects>.projectsContainer>.project_item>.bottom {}

.projects>.projectsContainer>.project_item h3,
.projects>.projectsContainer>.project_item h2 {
  margin: 0;
}

.project_icon {
  background-color: white;
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  padding-inline: 19px;
  padding-block: 4px;
  transition-duration: 0.4s;
}

.project_icon:hover {
  box-shadow: 0px 0px 0px 0px #000000;
  transform: translate(4px, 4px);
  cursor: pointer;
}
.letsconnect{
  display: flex;
  padding-inline: 116px;
  padding-block: 40px;
  flex-direction: column;
  gap: 20px;
}
.letsconnect .connections_contianer{
  display: flex;
  gap:23px;
}
.connection_icon{
  background-color: white;
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  padding-inline: 20px;
  padding-block: 20px;
  transition-duration: 0.4s;
  display: flex;
}
.connection_icon:hover {
  box-shadow: 0px 0px 0px 0px #000000;
  transform: translate(4px, 4px);
  cursor: pointer;
}

.footer{
  border-block: solid black 3px;
  padding-inline: 1rem;
}
form>.input {
  flex: 1;
  outline: none;
  font-weight: bold;
  padding-inline: 20px;
  font-size: 21px;
  font-family: "Overpass Mono", monospace;
  border: solid black 3px;
  box-shadow: 4px 4px 0px 0px #000000;
  transition-duration: 0.2s;
}

form>.input:hover {
  box-shadow: 0px 0px 0px 0px #000000;
  transform: translate(4px, 4px);
  cursor: pointer;
}

form>.input:focus-visible {
  box-shadow: 0px 0px 0px 0px #000000;
  transform: translate(4px, 4px);
}

.heading>.right {
  flex: 0.7;
}

.button {
  border: solid black 3px;
  height: min-content;
  width: max-content;
  padding-inline: 22px;
  padding-block: 15px;
  font-weight: bold;
  font-size: 16px;
  box-shadow: 4px 4px 0px 0px #000000;
  transition-duration: 0.2s;
}

.button:hover {
  box-shadow: 0px 0px 0px 0px #000000;
  transform: translate(4px, 4px);
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .heading {
    padding-inline: 10px;
    flex-direction: column;
  }
  .letsconnect .connections_contianer {
    flex-wrap: wrap;
  }
  .experience>.collection>.item {
    flex-direction: column;
  }
  .experience {
    padding-inline: 10px;
    padding-block: 40px;
  } 
  .skills {
    padding-inline: 10px;
    padding-block: 40px;
}
.projects {
  padding-inline: 10px;
    padding-block: 40px;
}
.projects>.projectsContainer {
  
  flex-direction: column;
}
.projects>.projectsContainer>.project_item {
  max-width: none;
}
.letsconnect {
  padding-inline: 10px;
    padding-block: 40px;
}
.letsconnect > .button{
  font-size: 0.9rem;
}
.heading>.right>img{
  max-width: 100%;
}
  form>.input {
    padding-inline: 20px;
    padding-block: 1px;
    font-size: 14px;
    font-family: "Overpass Mono", monospace;
    border: solid black 3px;
    box-shadow: 4px 4px 0px 0px #000000;
    transition-duration: 0.2s;
}
.nav > .button{
  padding-inline: 0.5rem;
  margin-block: 1rem;
}
.experience>.collection>.item {
  gap:0;
}
}